.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.menu,
.quiz,.score {
  width: 500px;
  height: 500px;
  background: lightblue;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.menu button,
.quiz button,.score button{
  width: 300px;
  height: 50px;
  margin: 5px;
  border: none;
  border-radius: 15px;
  font-size: 20px;
  cursor: pointer;
}

/* button:active {
  background: orange;
  color: white;
} */

button:hover {
  background: orange;
  color: white;
}
.menu img {
  width: 50%;
  height: auto;
}